.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.logo{
  width: 300px;
  height: 300px;
  border: 2px solid white;
  
}

.formGroup{
  padding: 10px;
  margin: 10px;
}

.hide {
	display: none
}

.show {
	display: inline-block;
}

.padBt10{
  padding-bottom: 10px;
}

.marTp10{
  margin-top: 30px;
}


.intro {
  margin: 30px 0;
}
.repo {
  margin: 30px 0;
  text-align: center;
  width: 100%;
}
.card-body {
  width: 30%;
  height: 350px;
}

